* {
  box-sizing: border-box;
}

body {
  background-color: #e0e0e0;
  font-family: monospace;
  font-size: 16px;
}

.wrapper {
  width: 100%;
}

.grid {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

header {
  background-color: #fff;
  padding: 20px 0;
  box-shadow: 0px 3px 5px 0px #cccccc;
}

nav a {
  font-weight: 700;
  margin-right: 20px;
}

nav a:last-child {
  margin-right: 0;
}

.active {
  border-bottom: 5px solid #606c76;
  padding-bottom: 7px;
}

footer {
  padding-bottom: 20px;
}

footer p {
  color: #a9a9a9;
}

.pt-2 {
  padding-top: 30px;
}

.pb-2 {
  padding-bottom: 30px;
}

.pb-3 {
  padding-bottom: 60px !important;
}

.mb-0 {
  margin-bottom: 0;
}

/*--------------------------------------------------------*/

.App {
  text-align: center;
}

/* ------------------ Grid Galerie ---------------------- */

.b-gal-grid .b-gal-w {
	display: grid;
	column-gap: 20px;
	row-gap: 20px;
	padding: 20px;

	grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}

.b-gal-grid.grid-2 .b-gal-w {
	@media screen and (min-width: 400px) {
		grid-template-columns: repeat(2, 1fr);
	}
}


.b-gal-grid.grid-3 .b-gal-w {
	@media screen and (min-width: 600px) {
		grid-template-columns: repeat(3, 1fr);
	}
}

.b-gal-grid.grid-4 .b-gal-w {
	@media screen and (min-width: 960px) {
		grid-template-columns: repeat(4, 1fr);
	}
}

.b-gal-grid.grid-5 .b-gal-w {
	@media screen and (min-width: 960px) {
		grid-template-columns: repeat(5, 1fr);
	}
}

/* ------------------ Swiper custom CSS ---------------------- */
:root {
  --swiper-theme-color: #9b4dca !important;
  --swiper-navigation-size: 24px !important;
  --swiper-pagination-bullet-inactive-opacity: 0.6;
}

.swiper-wrapper {
  height: 440px !important;
}

.swiper-slide {
  height: initial;
}

.swiper-slide img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}

.swiper-slide .titles {
  width: initial;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
  padding: 50px 60px 0;
  color: #fff;
  text-align: left;
}

.swiper-slide .titles h2 {
  position: relative;
  max-width: 500px;
  height: auto;
  font-size: 5rem;
  font-weight: 600;
}

.swiper-slide .titles p {
  font-size: 2rem;
}

.swiper-slide .titles a {
  background-color: #9b4dca;
  border: 0.1rem solid #9b4dca;
  border-radius: 0.4rem;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 700;
  height: 3.8rem;
  letter-spacing: .1rem;
  line-height: 3.8rem;
  padding: 0 3rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
}

.swiper-slide .titles a:hover,
.swiper-slide .titles a:focus {
  background-color: #606c76;
    border-color: #606c76;
    color: #fff;
    outline: 0;
}

.swiper-button-prev:after, .swiper-button-next:after {
  font-size: var(--swiper-navigation-size);
}

.swiper-pagination-bullet {
  background: #fff !important;
}

.swiper-pagination-bullet-active {
  background: var(--swiper-theme-color) !important;
}

@media only screen and (min-width: 500px) {
  .swiper-wrapper {
    height: 600px !important;
  }
  .swiper-slide .titles {
    padding: 150px 130px 0;
  }
  .swiper-slide .titles h2 {
    font-size: 6rem;
  }
  .swiper-slide .titles p {
    font-size: 2.6rem;
  }
  .swiper-button-prev:after, .swiper-button-next:after {
    font-size: 44px !important;
  }
}